import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useRef } from "react";
import Results from "../../Tracking/Results";
import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import { getParcelDetails, setDetails } from "../../../redux/reducers/ordering";
import { useLayoutEffect } from "react";
import { useLocation } from "react-router";

const Tracking = ({ setLoading }) => {
  const dispatch = useDispatch();
  const location = useLocation();

  const formik = useFormik({
    initialValues: {
      id: "",
    },
    onSubmit: (values) => {
      setLoading(true);

      dispatch(getParcelDetails(values.id));
    },
  });

  const firstUpdate = useRef(true);

  const { details } = useSelector(({ ordering }) => ordering);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // hide preloader
    setLoading(false);
  }, [details]);

  useEffect(() => {
    dispatch(setDetails(null));
  }, [location.pathname]);

  return (
    <section>
      <Typography variant="h3" mb={5}>
        Отледить заказ
      </Typography>
      <Box
        component="form"
        onSubmit={formik.handleSubmit}
        sx={{
          maxWidth: { xs: "100%", lg: 567 },
        }}
      >
        <Typography varian="body2" sx={{ fontWeight: "normal" }} mb={3}>
          Достаточно ввести номер заказа или экспресс-накладной в поле для
          отслеживания и нажать кнопку «Отследить».
        </Typography>
        <Box
          display="flex"
          wrap="nowrap"
          sx={{
            width: "100%",
            flexDirection: { xs: "column", lg: "row" },
          }}
        >
          <TextField
            variant="outlined"
            fullWidth
            name="id"
            onChange={formik.handleChange}
            value={formik.values.id}
            placeholder="Введите номер заказа"
          />
          <Button
            variant="contained"
            size="large"
            color="primary"
            type="submit"
            sx={{
              ml: {
                xs: 0,
                lg: 2,
              },
              mt: { xs: 2, lg: 0 },
            }}
          >
            Отследить
          </Button>
        </Box>
      </Box>
      {details?.status === 404 ? (
        <Typography variant="h4" mt={4}>
          По вашему запросу ничего не найдено :)
        </Typography>
      ) : (
        details?.id && <Results details={details} />
      )}
    </section>
  );
};

export default Tracking;
