import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Step,
  StepLabel,
  Stepper,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getStatusesData } from "../../redux/reducers/ordering";

const Results = ({ details }) => {
  const classes = useStyles();
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down("lg"));
  const dispatch = useDispatch();
  const { statuses } = useSelector(({ ordering }) => ordering);

  useEffect(() => {
    dispatch(getStatusesData());
  }, []);

  return (
    <Box className={classes.paper} mt={8}>
      <Typography variant="body2">{details?.code}</Typography>
      <Box display="flex" justifyContent="space-between" mt={2} mb={3}>
        <div>
          <Typography variant="subtitle1" sx={{ fontWeight: "500" }} mb={1}>
            {new Date(details.sending_date).toLocaleDateString()}
          </Typography>
          <Typography variant="body2">
            {details.direction[0]?.district.region},{" "}
            {details.direction[0]?.district.name}
          </Typography>
        </div>
        <div>
          <Typography
            variant="subtitle1"
            sx={{ fontWeight: "500", textAlign: "end" }}
            mb={1}
          >
            {details.status?.id == 5 ? <>03.01.2022</> : "-"}
          </Typography>
          <Typography variant="body2">
            {details.direction[1]?.district.region},{" "}
            {details.direction[1]?.district.name}
          </Typography>
        </div>
      </Box>
      <Stepper
        activeStep={
          details?.status == 6
            ? 1
            : details?.status == 7
            ? 2
            : details?.status == 8
            ? 3
            : details?.status == 9
            ? 3
            : details?.status == 10
            ? 4
            : details?.status == 11
            ? 6
            : 7
        }
        alternativeLabel
      >
        {Array.isArray(statuses) &&
          statuses?.reverse()?.map((status) => (
            <Step key={status.id}>
              <StepLabel>
                <Typography variant="subtitle1">{status.title}</Typography>
              </StepLabel>
            </Step>
          ))}
      </Stepper>
      <Accordion className={classes.accordion}>
        <AccordionSummary style={{ width: "100" }}>
          <Typography
            variant="body2"
            sx={{
              textAlign: "center",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              width: "100%",
            }}
          >
            <span style={{ textDecoration: "underline" }}>
              Просмотреть описание груза
            </span>
            <i
              className="ri-arrow-down-s-line"
              style={{
                marginLeft: 24,
                fontSize: "24px",
                display: "inline-block",
              }}
            />
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Праметры груза
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {details.dimension.height}х{details.dimension.width}х
                {details.dimension.length}, до {details.dimension.weight} кг
              </Typography>
            </Grid>
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Тип доставки
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {details.payment.delivery_type.title}
              </Typography>
            </Grid>
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Упаковка
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {details.payment.packaging[0].title}
              </Typography>
            </Grid>
            {!lg && <div className={classes.line} />}
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Пункт отправки
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {details.direction[0]?.district.region},{" "}
                {details.direction[0]?.district.name}
              </Typography>
            </Grid>
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Дата сдачи груза
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {new Date(details?.sending_date).toLocaleDateString()}{" "}
                {new Date(details?.sending_date).toLocaleTimeString([], {
                  timeStyle: "short",
                })}
              </Typography>
            </Grid>
            <Grid item xs={12} md={6} lg={4} className={classes.grid}>
              <Typography variant="body1" sx={{ fontWeight: "500" }} mb={1}>
                Пункт получения
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                {details.direction[1]?.district.region},{" "}
                {details.direction[1]?.district.name}
              </Typography>
            </Grid>
          </Grid>
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

export default Results;

const useStyles = makeStyles((theme) => ({
  paper: {
    background: "#FFFFFF",
    border: "1px solid #C8C8C8",
    boxSizing: "border-box",
    borderRadius: "20px",
    padding: "40px  70px",
    [theme.breakpoints.down("md")]: {
      padding: 30,
    },

    "& .MuiStepConnector": {
      minWidth: 39,
    },

    "& .MuiStep-root": {
      padding: "0 30px",
    },

    "& .MuiStepper-root": {
      overflowX: "scroll",
      overflowY: "hidden",
    },

    "& .Mui-active": {
      color: "#00000061!important",
    },
  },

  accordion: {
    marginTop: 40,
    boxShadow: "none!important",

    "&::before": {
      display: "none",
    },
  },

  grid: {
    [theme.breakpoints.down("lg")]: {
      borderBottom: "1px solid #EBEBEB",
      paddingBottom: 20,

      "&:last-child, &:nth-child(5)": { border: "none" },
    },

    [theme.breakpoints.down("md")]: {
      "&:nth-child(5)": { borderBottom: "1px solid #EBEBEB" },
    },
  },

  line: {
    height: 1,
    width: "100%",
    background: "#EBEBEB",
    margin: "24px 0",
  },
}));
